import React, { useState } from "react";
import "./styles.css";

const CounterText = (props) => {
  return (
    <p>
      <b>{props.name}</b> counter: {props.count}
    </p>
  );
};

const INITIAL_COUNT = 0;

const Counter = (props) => {
  const [count, setCount] = useState(INITIAL_COUNT);
  const countAdd = () => setCount((prevCount) => prevCount + 1);
  const countSub = () => setCount((prevCount) => prevCount - 1);
  const countReset = () => setCount(INITIAL_COUNT);

  return (
    <>
      <CounterText count={count} name={props.name} />

      <button onClick={countAdd}>+1</button>
      <button onClick={countSub}>-1</button>
      <button onClick={countReset}>Reset</button>
    </>
  );
};

export default function App() {
  return (
    <>
      <Counter name="Push" />
      <Counter name="Pull" />
      <Counter name="Run" />
    </>
  );
}
